import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { TableDataEditRow } from 'components/common/index';
import Btn from 'components/common/button/Btn';
import { Card, Row, Form, Col, message, Input, Button } from 'antd';
import {
  getStudents,
  updateTrainingScore,
  lockStudent,
  unlockStudent,
  d∆°nloadFile,
} from 'services/TrainingScore';
import { getQueryParams } from 'utils/urlUtil';

import FormSearchTrainingScore from './FormSearchTrainingScore';

class TrainingScorePage extends Component {
  state = {
    paramSearch: [],
    reload: 0,
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  // eslint-disable-next-line consistent-return
  handleSaveRowData = async rowData => {
    const {
      score,
      studentId,
      note,
      semesterId,
      noOfSocialDay,
      criteriaTypeId,
      yearEduId,
    } = rowData;
    console.log('rowData',rowData);
    if (!score) {
      message.error('Vui l√≤ng ch·ªçn ch·ª©c danh ch√≠nh');
      return null;
    }

    const dataUpdate = {
      studentId,
      semesterId,
      yearEduId,
      criteriaTypeId,
      score,
      noOfSocialDay,
      note,
    };

    try {
      await updateTrainingScore(dataUpdate);
      message.success('C·∫≠p nh·∫≠t th√†nh c√¥ng');
      this.setState({
        reload: this.state.reload + 1,
      });
      return {};
    } catch (e) {
      message.error(e.response.data.message);
      return null;
    }
  };

  dataFormSeach = data => {
    this.setState({
      paramSearch: data,
      reload: this.state.reload + 1,
    });
  };

  lockStudent = async record => {
    const { isLocked, studentId, semesterId, yearEduId, criteriaTypeId } = record;
    const dataUpdate = {
      studentId,
      semesterId,
      yearEduId,
      criteriaTypeId,
    };
    if (isLocked) {
      try {
        await unlockStudent(dataUpdate);
        message.success('M·ªü kho√° th√†nh c√¥ng');
        this.setState({
          reload: this.state.reload + 1,
        });
        return null;
      } catch (e) {
        message.error(e.response.data.message);
        return null;
      }
    } else {
      try {
        await lockStudent(dataUpdate);
        message.success('Kho√° th√†nh c√¥ng');
        this.setState({
          reload: this.state.reload + 1,
        });
        return null;
      } catch (e) {
        message.error(e.response.data.message);
        return null;
      }
    }
  };

  dowloadFilePdf = () => {
    d∆°nloadFile(this.state.paramSearch);
  };

  render() {
    const columns = [
      {
        title: 'M√£ sinh vi√™n',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H·ªç t√™n',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ng√†y sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'S·ªë ng√†y c√¥ng t√°c XH',
        dataIndex: 'noOfSocialDay',
        key: 'noOfSocialDay',
        editable: true,
        renderFormItemEdit: ({ form, record }) => (
          <Form.Item>
            {form.getFieldDecorator('noOfSocialDay', {
              initialValue: record.noOfSocialDay,
            })(<Input />)}
          </Form.Item>
        ),
      },
      {
        title: 'ƒêi·ªÉm RL',
        dataIndex: 'score',
        key: 'score',
        editable: true,
        renderFormItemEdit: ({ form, record }) => (
          <Form.Item>
            {form.getFieldDecorator('score', {
              initialValue: record.score,
            })(<Input />)}
          </Form.Item>
        ),
      },
      {
        title: 'Ghi ch√∫',
        dataIndex: 'note',
        key: 'note',
        editable: true,
        renderFormItemEdit: ({ form, record }) => (
          <Form.Item>
            {form.getFieldDecorator('note', {
              initialValue: record.note,
            })(<Input />)}
          </Form.Item>
        ),
      },

    ];

    return (
      <Card title="Nh·∫≠p ƒëi·ªÉm r√®n luy·ªán">
        <Row>
          <Col span={24}>
            <FormSearchTrainingScore dataForm={this.dataFormSeach} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableDataEditRow
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              formSearch="true"
              service={getStudents}
              columns={columns}
              handleSaveRowData={this.handleSaveRowData}
              allowGetData={!!this.state.paramSearch.criteriaTypeId}
              customBtnAction={({ record, btnAction }) => {
                if (record.isLocked) {
                  return (
                    <Button
                      type={record.isLocked ? 'danger' : 'primary'}
                      shape="circle"
                      icon={record.isLocked ? 'lock' : 'unlock'}
                      onClick={() => this.lockStudent(record)}
                    />
                  );
                }
                return (
                  <Fragment>
                    {btnAction}
                    <Button
                      type={record.isLocked ? 'danger' : 'primary'}
                      shape="circle"
                      icon={record.isLocked ? 'lock' : 'unlock'}
                      onClick={() => this.lockStudent(record)}
                    />
                  </Fragment>
                );
              }}
              rowKey="code"
            />
          </Col>
          <Col offset={8} span={12}>
            <Btn
              style={{ height: 35, marginLeft: 8, marginRight: 8 }}
              onClick={this.dowloadFilePdf}
              icon="download"
            >
              Download
            </Btn>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TrainingScorePage />
  </Page>
);



