import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';

const { Option } = Select;

@Form.create({ name: 'FormSearchTrainingScore' })
class FormSearchTrainingScore extends Component {
  state = {
    dataCriteria: [
      { id: 'PTCSV', label: 'Ph√≤ng CTSV ƒë√°nh gi√°' },
      { id: 'LT', label: 'L·ªõp tr∆∞·ªüng ƒë√°nh gi√°' },
    ],
  };
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      this.props.dataForm(values);
      udpateQueryParams(values);
    });
  };
  render() {
    const { dataCriteria } = this.state;
    const { getFieldDecorator } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui l√≤ng ch·ªçn H·ªá!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui l√≤ng ch·ªçn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui l√≤ng ch·ªçn Kh√≥a',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui l√≤ng ch·ªçn CN',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui l√≤ng ch·ªçn NƒÉm h·ªçc',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui l√≤ng ch·ªçn H·ªçc k·ª≥',
          },
        ],
      },
    };
    return (
      <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <Col span={12}>
            <Form.Item label="Ti√™u ch√≠ ƒë√°nh gi√°">
              {getFieldDecorator('criteriaTypeId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui l√≤ng ch·ªçn Ti√™u ch√≠ ƒë√°nh gi√°',
                  },
                ],
              })(
                <Select>
                  {dataCriteria.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchTrainingScore;



