import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getStudents = dataSearch =>
  request({
    prefix,
    url: `/TrainingScore/GetStudentForTrainingScore`,
    params: { ...dataSearch },
    method: 'GET',
  });

export const updateTrainingScore = dataStudent =>
  request({
    prefix,
    url: `/TrainingScore/UpdateTrainingScores`,
    data: [{ ...dataStudent }],
    method: 'POST',
  });

export const lockStudent = dataStudent =>
  request({
    prefix,
    url: `/TrainingScore/LockTrainingScores`,
    data: [{ ...dataStudent }],
    method: 'POST',
  });

export const unlockStudent = dataStudent =>
  request({
    prefix,
    url: `/TrainingScore/UnlockTrainingScores`,
    data: [{ ...dataStudent }],
    method: 'POST',
  });

export const d∆°nloadFile = dataSearch =>
  requestDownload({
    defaultFileName: 'fileDownload.xlsx',
    prefix,
    url: `/TrainingScore/ExportTrainingScores`,
    data: { ...dataSearch },
    method: 'POST',
  });



